}

.SearchList {
    position: relative;
    /*width: 100%;*/
    max-height: 200px;
    overflow: auto;
}

.select ul .cityInput {
    width: 100%;
    border: none;
    position: relative;
}

.select ul .cityInput input {
    border: none;
    border-radius: 15px;
    height: 40px;
    width: 100%;
    font-size: 25px;
    padding-left: 20px;
    padding-right: 20px;
    box-sizing: border-box;
}

.select ul .cityInput input:focus {
    outline: none;
}

.cityInput .searchIcon {
    position: absolute;
    right: 10px;
    top: calc(50% - 12px);
    font-size: 25px;
    color: grey;
    -webkit-transform: none;
            transform: none;
    -webkit-transition: color .5s, -webkit-transform .5s;
    transition: color .5s, -webkit-transform .5s;
    transition: color .5s, transform .5s;
    transition: color .5s, transform .5s, -webkit-transform .5s;
}

.cityInput > input:focus + .searchIcon {
    color: blue;
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}

.select > button {
    position: relative;
    border: none;
    width: 100%;
}

.select__arrow {
    position: absolute;
    top: calc(50% - 15px);
    right: 15px;
}

.select__location {
    margin-left: 10px;
}

.select__arrow-rotate {
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}

.arrow-active {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

